import React, { useState, useEffect } from "react";

//  
const Converter = () => {
  const [currencies, setCurrencies] = useState([]); //   
  const [fromCurrency, setFromCurrency] = useState("USD"); //  
  const [toCurrency, setToCurrency] = useState("EUR"); // ,   
  const [amount, setAmount] = useState(1); //   
  const [convertedAmount, setConvertedAmount] = useState(null); //  
  const [loading, setLoading] = useState(true); //  
  const [error, setError] = useState(null); // 

  const apiKey = "415bf1c5fd54503fa6a8cc96"; //    API
  const apiUrl = `https://v6.exchangerate-api.com/v6/${apiKey}/latest/USD`;

  //     
  useEffect(() => {
    const fetchCurrencies = async () => {
      setLoading(true);
      try {
        const response = await fetch(apiUrl);
        const data = await response.json();

        if (data.result === "success") {
          const currencyList = Object.keys(data.conversion_rates); //   
          setCurrencies(currencyList); //  
        } else {
          setError(data.error);
        }
      } catch (err) {
        console.error(" :", err);
        setError("   ");
      } finally {
        setLoading(false);
      }
    };

    fetchCurrencies();
  }, []); //      

  //    
  const convertCurrency = async (amount) => {
    setLoading(true);
    try {
      const response = await fetch(
        `https://v6.exchangerate-api.com/v6/${apiKey}/latest/${fromCurrency}`
      );
      const data = await response.json();

      if (data.result === "success") {
        const conversionRate = data.conversion_rates[toCurrency];
        setConvertedAmount((amount * conversionRate).toFixed(2)); // 
      } else {
        setError("  ");
      }
    } catch (err) {
      console.error("  :", err);
      setError("  ");
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    if (currencies.length > 0) {
      convertCurrency(amount); //    
    }
  }, [fromCurrency, toCurrency, amount, currencies]); //    

  const handleFromCurrencyChange = (e) => {
    setFromCurrency(e.target.value);
  };

  const handleToCurrencyChange = (e) => {
    setToCurrency(e.target.value);
  };

  const handleAmountChange = (e) => {
    setAmount(e.target.value);
  };

  //  ,   
  const renderCurrencyOptions = () => {
    if (loading) {
      return <option value=""> ...</option>;
    }
    if (error) {
      return <option value="">  </option>;
    }
    if (currencies.length === 0) {
      return <option value="">  </option>;
    }

    return currencies.map((currency) => (
      <option key={currency} value={currency}>
        {currency}
      </option>
    ));
  };

  return (
    <div className="converter">
      <h1> </h1>
      <div>
        <input
          type="number"
          value={amount}
          onChange={handleAmountChange}
          placeholder=" "
        />
        <div>
          <label>  :</label>
          <select value={fromCurrency} onChange={handleFromCurrencyChange}>
            {renderCurrencyOptions()}
          </select>
        </div>

        <div>
          <label>  :</label>
          <select value={toCurrency} onChange={handleToCurrencyChange}>
            {renderCurrencyOptions()}
          </select>
        </div>

        {loading ? (
          <p>...</p>
        ) : error ? (
          <p>: {error}</p>
        ) : (
          <div>
            <h2>
              : {convertedAmount} {toCurrency}
            </h2>
          </div>
        )}
      </div>
    </div>
  );
};

export { Converter };
